@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
@font-face {
  font-family: "Cafe24SsurroundAir";
  src: url(/web-dipping/static/media/Cafe24SsurroundAir.d82d9a3c.woff2) format("woff");
}

input {
  all: unset;
  -webkit-appearance: none;
          appearance: none;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Cafe24SsurroundAir",
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  font-weight: bold;
  margin: 0px;
}

body {
  background-color: #142f43;
  font-family: -apple-system, BlinkMacSystemFont, "Cafe24SsurroundAir",
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  font-size: 14px;
  font-weight: bold;
  color: white;

  padding: 0px 0.75rem;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
}

.logOut {
  margin-top: 50px;
  max-width: 256px;
}

.title {
  margin-top: 50px;
  font-size: 2rem;
}

